
@keyframes moveUp1 {
    0% {
        transform: translate(0, 0);
    }
    100% {
        transform: translate(0, -35px);
    }
}

@keyframes moveUp {
    0% {
        transform: translate(0, 0);
    }
    100% {
        transform: translate(0, -50px);
    }
}

@keyframes moveLeft {
    0% {
        width: 0;
    }
    100% {
        width: 330px;
        border-bottom: 1px solid $yellow;
    }
}

@keyframes makeItBig {
    0% {
        transform: scale(1);
    };
    50%{
        transform: scale(1.05);
        text-shadow: 5px 5px 1px black;
    };
    100%{
        transform: scale(1);
    };
}

@keyframes animatetop {
    from {
        top:-300px; 
        opacity:0;
    }
    to {
        top:0; 
        opacity:1;
    }
}